<template>
	<el-container class="xblayout-container">
	  	<el-header class="xblayout-header">
	  		<navbar></navbar>
	  	</el-header>
	  	<el-container>
		    <el-aside width="200px" class="xblayout-sidebar">
		    	<sidebar></sidebar>
		    </el-aside>
		    <el-container>
		      	<el-main class="xblayout-main">
		      		<app-main></app-main>
		      	</el-main>
		      <!--<el-footer class="xblayout-footer">Footer</el-footer>-->
		    </el-container>
	  	</el-container>
	</el-container>
</template>

<script>
import { Navbar, Sidebar, AppMain } from 'views/layout';
export default {
    name: 'layout',
    components: {
    	Navbar,
      	Sidebar,
      	AppMain
    },
    computed: {
	   
  	},
    
}
</script>

<style rel="stylesheet/scss" lang="scss" slot-scope="scoped">
	.xblayout-header{
		background-color:#4f79f2;
	}
	
</style>



