<template>
	<div class="left-menu-box">
		<el-row class="tac">
			  <el-col :span="24">
			    
			    <el-menu :default-active="active" class="el-menu-vertical-demo" router>
			    	<template v-for="(item,index) in menuData" >
			    		<!--多菜单-->
			    		<el-submenu :index="item.MENU_ID" v-if="item.subMenu ? !selectFlag :selectFlag">
					        <template slot="title">
					        	 <i :class="item.MENU_ICON"></i>
					            <span>{{item.MENU_NAME}}</span>
					        </template>
					        <!--多菜单中  单-->
					        <el-menu-item-group v-for="childItem in item.subMenu" key="childItem.MENU_ID" v-if="childItem.subMenu==null ? !childSelectFlag:childLevelFlag">
					        	<i :class="childItem.MENU_ICON"></i>
					            <el-menu-item :index="childItem.MENU_ID" :route="{path:childItem.MENU_URL}">{{childItem.MENU_NAME}}</el-menu-item>
					        </el-menu-item-group>
					        <!--多菜单  多 -->
					       <el-submenu v-for = "childItem in item.subMenu" key ="childItem.MENU_ID" :index="childItem.MENU_ID" v-if="childItem.subMenu ? !childSelectFlag:childSelectFlag">
					          <template slot="title">
					        	<i :class="childItem.MENU_ICON"></i>
					          	<span>{{childItem.MENU_NAME}}</span>
					          </template>
					          <el-menu-item :index="grandson.MENU_ID" v-for="grandson in childItem.subMenu" key = "grandson.MENU_ID" :route="{path:grandson.MENU_URL}">{{grandson.MENU_NAME}}</el-menu-item>
					        </el-submenu>
					    </el-submenu>
					    <!--单菜单-->
					    <el-menu-item :index="item.MENU_ID" v-if="item.subMenu ? levelFlag :!levelFlag" :route="{path:item.MENU_URL}">
					        <span slot="title">{{item.MENU_NAME}}</span>
					    </el-menu-item>
				  	</template>
				</el-menu>
			    
			    
			</el-col>
		</el-row>
	</div>

    
</template>


<script>
import {
	leftMenuPort
} from 'api/model/leftMenu';   //左菜单数据
import {
  	getUserInfo,
  	getUsers
} from 'utils/auth';
export default {
    components: { 
    },
	data(){
    	return {
    		levelFlag:false,
    		selectFlag:false,
    		childLevelFlag:false,
    		childSelectFlag:false,
    		active:'1',
    		menuData:[{
			    MENU_ICON:'',
			    MENU_ID:'',
			    MENU_NAME:'',
			    MENU_ORDER:'',
			    MENU_STATE:'',
			    MENU_TYPE:'',
			    MENU_URL:'',
			    MPLATFORM:'',
			    PARENT_ID:'',
			    hasMenu:true,
			    parentMenu:'',
			    subMenu:[],
	    	}],
	    		
    		
    	}
    },
    created(){
    	this.leftMenu()
    },
    methods:{
    
   		leftMenu(){
   			let req = {
	    		USER_ID:getUsers(),  // 
	    		RIGHTS:getUserInfo().RIGHTS,
	    		PLATFORM:'1'    //系统平台
    	    }
   			
	    	leftMenuPort(req).then( data => {
	    	 	this.menuData = data
	//	  		console.log('菜单::'+JSON.stringify(data))
		  		
		    })
   		}
    }
}
</script>

<style rel="stylesheet/scss" lang="scss" slot-scope="scoped">
  	.left-menu-box{
  		background: #fff;
  		height: 100%;
  		
  	}
  	/*.el-menu-item,.el-submenu__title{
		width: 200px ;
	}*/
</style>



